@import url(https://fonts.googleapis.com/css2?family=Merriweather&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Roboto&display=swap);
* {
  padding: 0;
  margin: 0;
  font-family: 'Roboto', sans-serif;
  text-decoration: none;
}

.App {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.text-color-black {
  color: #404040;
}
.SearchBar {
  display: flex;
}

.search-container {
  max-width: 700px;
  background: rgba(241, 241, 241, .86);
  border-radius: 45px;
  box-shadow: 1px 3px 3px 1px rgba(0, 0, 0, 0.2);
  margin: auto;
  transition: 0.2s ease;
  padding-right: 15px;
  display: flex;
  flex-direction: row;
  align-content: center;
}

.search-container:hover {
  background: #e9e9e9;
}

.search-input {
  color: #333;
  margin: auto;
  max-width: 700px;
  min-width: 400px;
  height: 40px;
  border: none;
  outline: none;
  background: none;
  font-size: 16px;
  padding: 3px 0px 3px 25px;
  line-height: 60px;
  border-radius: 45px;
  transition: min-width 0.5s cubic-bezier(0.79, -0.27, 0.21, 1.4);
}

.search-input:focus {
  min-width: 500px;
}

::-webkit-input-placeholder {
  font-size: 14px;
  color: #696969;
}

:-ms-input-placeholder {
  font-size: 14px;
  color: #696969;
}

::-ms-input-placeholder {
  font-size: 14px;
  color: #696969;
}

::placeholder {
  font-size: 14px;
  color: #696969;
}

.select-wrapper {
  position: relative;
  width: 83px;
}

.select-wrapper:after {
  content: '\f078';
  font-family: 'Font Awesome 5 Free';
  font-weight: 600;
  font-size: 14px;
  color: #4d4d4d;
  pointer-events: none;
  position: absolute;
  z-index: 1;
  right: 4px;
  top: 15px;
  height: 100%;
}

#filter {
  background: transparent;
  font-size: 14px;
  color: #4d4d4d;
  border: none;
  outline: none;
  height: 100%;
  width: 100%;
  padding: 3px;
  cursor: pointer;
  -moz-appearance:none;
  -webkit-appearance:none; 
  appearance: none;
  transition: 0.4s ease-in;
}

@media only screen and (max-width: 600px) {
  .search-input {
    height: 35px;
    font-size: 15px;
    min-width: 210px;
  }

  .search-input:focus {
    min-width: 240px; 
  }

  .select-wrapper:after {
    top: 13px;
  }
}

@media only screen and (max-width: 320px) { 

  .search-input {
    height: 32px;
    font-size: 14px;
    min-width: 195px;
    padding: 3px 0px 3px 15px;
  }

  .search-input:focus {
    min-width: 195px; 
  }
  #filter{
    font-size: 14px;
  }

}
.Results {
  max-width: 510px;
  width: 100%;
  min-width: 300px;
  display: flex;
  flex-wrap: wrap;
  margin-top: 30px;
}

.SingleResult {
  width: 100%;
  margin: 6px;
  padding: 12px;
  border-radius: 12px;
  background: rgba(241, 241, 241, .85);
  transition: .22s ease-in-out;
  box-shadow: 1px 3px 3px 1px rgba(94, 94, 94, 0.2);
  color: #3d3d3d;
  display: flex;
}

.SingleResult:hover {
  transform: scale(1.02);
  box-shadow: 0px 0px 4px 2px rgba(204, 204, 204, 0.5);
  background: rgba(241, 241, 241, 1);
  color: #1d1d1d;
}

.TitlesBox {
  width: 70%;
}

.TitleName, .AuthorName {
  font-size: 16px;
  width: 100%;
  margin: 5px 0;
  margin-left: 18px;
  font-weight: 500;
}

.TitleName {
  font-weight: bold;
}

.Buttons {
  width: 25%;
  display: flex;
}

.ButtOpenNewTab, .ButtDownloadFile {
  margin: auto;
  font-size: 22px;
  /* color: rgba(125, 181, 207, 1); */
  color: #404040;
  transition: .3s ease;
  /* text-shadow: 0px 0px 2px rgba(143, 143, 143, 0.4); */
}

.ButtOpenNewTab:hover, .ButtDownloadFile:hover {
  color: rgb(142, 209, 240);
}

@media only screen and (max-width: 600px) {
  .SingleResult {
    margin: 5px;
  }
  .ButtOpenNewTab, .ButtDownloadFile {
    font-size: 18px;
  }
  .TitleName, .AuthorName {
    font-size: 15px;
    margin-left: 6px;
  }
}

@media only screen and (max-width: 320px) {
  .SingleResult {
    padding: 6px;
    margin: 4px;
  }
  .TitleName, .AuthorName {
    font-size: 14px;
  }
}
.dashboard-container {
  width: 80%;
  min-height: 80vh;
  background-color: #f1f1f1;
  border-radius: 20px;
  box-shadow: 4px 4px 12px rgba(0, 0, 0, 0.3);
  margin: 60px;
  padding: 80px;
  color: #404040;
  justify-content: center;
  align-items: center;
}

.flex-row {
  display: flex;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.container {
  width: 100%;
  height: auto;
}

.header-container {
  height: auto;
  justify-content: space-between;
  width: 100%;
  max-width: 1000px;
}

.header-text {
  padding: 0 10px;
}

.icon {
  display: block;
  padding: 10px;
  font-size: 24px;
  cursor: pointer;
  transition: color 0.2s ease-in;
}

.icon:hover {
  color: #111;
}

.books-list-header {
  height: 40px;
  background-color: #404040;
  align-items: center;
  justify-content: space-around;
  padding: 5px;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
}

.booklist-header-text {
  color: #f1f1f1;
  font-size: 22px;
  width: 170px;
}

.books-list {
  margin-top: 30px;
  border: #404040 solid 2px;
  border-radius: 10px;
  max-width: 1000px;
  text-align: center;
}

.book-element {
  height: 70px;
  padding: 20px;
  transition: color 0.2s ease-out;
  align-items: center;
  justify-content: space-around;
  text-overflow: ellipsis;
  overflow: hidden;
  max-lines: 2;
}

.book-element:last-child {
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}

.book-element:hover {
  background-color: #e1e1e1;
}

.title-text {
  font-size: 19px;
  width: 170px;
}

#closer {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  z-index: 9;
}

.add-book-form-container {
  max-width: 700px;
  width: 100%;
  min-height: 600px;
  background-color: #f1f1f1;
  border-radius: 20px;
  box-shadow: 4px 4px 12px rgba(0, 0, 0, 0.3);
  position: fixed;
  z-index: 10;
  margin: auto;
  padding: 25px;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.add-book-form {
  justify-items: center;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: 100%;
}

.input {
  background: none;
  border: none;
  border-bottom: solid 1px #404040;
  width: 100%;
  max-width: 600px;
  height: 35px;
  outline: none;
  margin: 10px;
}

.submitBook {
  padding: 8px 15px;
  color: #f1f1f1;
  font-size: 18px;
  font-weight: bold;
  background-color: #404040;
  border: none;
  border-radius: 8px;
  outline: none;
  cursor: pointer;
}

.delete-book {
  background-color: #ef4052;
}

.buttons-container {
  margin-top: 50px;
  width: 200px;
  justify-content: space-around;
}

.booklist-header-text, .title-text {
  font-size: 18px;
}

@media only screen and (max-width: 1300px) {
  .title-text {
    font-size: 16px;
  }

  .booklist-header-text {
    font-size: 16px;
  }

  .book-element {
    height: 30px;
  }

  .dashboard-container {
    border-radius: 0;
    width: 100%;
    height: 100vh;
    padding: 0;
    margin: 0;
  }
}

@media only screen and (max-width: 800px) {
  .books-list {
    border: #404040 solid 1px;
  }
  .dashboard-container{
    width: 100%;
    height: 100vh;
    margin: 0;
    border-radius: 0;
    padding: 60px 0;
  }
}

@media only screen and (max-width: 500px) {
  .books-list {
    border: #404040 solid 1px;
  }
  .dashboard-container{
    padding: 25px 0;
  }
  .header-text {
    font-size: 22px;
    padding: 10px;
  }
  .icon {
    font-size: 18px;
  }
  .books-list-header {
    border-radius: 0;
  }
  .books-list {
    border-radius: 0;
    margin-top: 15px;
  }
  .submitBook {
    font-size: 15px;
  }

  .booklist-header-text, .title-text {
    width: 80px;
  }
}

@media only screen and (max-width: 350px) {
  .booklist-header-text, .title-text {
    font-size: 13px;
  }
}
.LoginFormContainer {
  width: 480px;
  height: 530px;
  background-color: rgba(241, 241, 241, .95);
  border-radius: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: space-around;
}

.input-container {
  width: 350px;
  height: 40px;
  padding: 20px;
  display: flex;
  justify-content: center;
}

.loginForm {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 480px;
}

input[type=text], input[type=password] {
  background: none;
}

.text-input {
  width: 100%;
  border: none;
  border-bottom: solid 1px #404040;
  background-color: none;
  outline: none;
  padding: 15px 5px;
}

.submitButton {
  padding: 8px 15px;
  color: #f1f1f1;
  font-size: 18px;
  font-weight: bold;
  background-color: #404040;
  border: none;
  border-radius: 8px;
  outline: none;
  cursor: pointer;
}

@media only screen and (max-width: 800px) {
  .LoginFormContainer {
    width: 100%;
    height: 100vh;
    border-radius: 0;
  }
}

@media only screen and (max-width: 500px) {
  .input-container {
    max-width: 360px;
    min-width: 280px;
    width: 100%;
    height: auto;
    padding: 12px 0;
    margin: 0 2%;
  }
  .submitButton {
    font-size: 16px;
  }
  .text-input {
    padding: 10px 5px;
  }
}
